<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Registrar nuevo camion:</h4>

		<b-form>
			<!-- nro_ticket -->
			<b-form-group
				label="*Patente"
				label-for="patente"
				@submit.stop.prevent="handleSubmit"
			>
				<b-form-input
					id="patente"
					v-model="camion.patente"
					type="text"
					placeholder="Ingrese el numero del patente"
					:state="validacion.patente.estado"
					invalid-feedback="Complete este campo"
					required
				>
				</b-form-input>
				<b-form-invalid-feedback id="patente-live-feedback">
					{{ validacion.patente.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button
			class="mt-2"
			variant="success"
			bk
			@click="postCamion()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				camion: {},
				data: {},
				validacion: {
					patente: { estado: null, mensaje: "" },
				},
				respuesta: {},
			};
		},
		methods: {
			async getCamiones() {
                let CamionesAPI = new APIControler();
                this.data = await CamionesAPI.getData();
			},
			async postCamion() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "camiones/";
				this.respuesta = await gastoSalienteAPI.postData(this.camion);
				this.cargarFeedback();
				this.updateTable();
			},

			cargarFeedback() {
				let valido;
				this.respuesta=this.respuesta|| {}
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
	};
</script>

