<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>

		<b-form>
			<!-- nro_ticket -->
			<b-form-group label="*Patente"
						  label-for="patente"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="patente"
							  v-model="camion.patente"
							  type="text"
							  placeholder="Ingrese el numero del patente"
							  :state="validacion.patente.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="patente-live-feedback">
					{{ validacion.patente.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button class="mt-2"
				  variant="success"
				  bk
				  @click="putCamion()">Modificar</b-button>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
	import axios from "axios";

	export default {
		props: {
			camion: {},
			updateTable: Function,
		},
		data() {
			return {
                camiones: {},
				data: {},
				options: [{ value: null, text: "Elija una gasto", disabled: true }],

				validacion: {
					patente: { estado: null, mensaje: "" },
				},
			};
		},
		created: function () {
			this.getCamiones();
		},
		methods: {
            async getCamiones() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "camiones/";
				this.data = await gastoSalienteAPI.getData(this.camiones);
				this.data.forEach((element) => {
					let option = {};
					option.value =
						"https://arandadistribucion.pythonanywhere.com/camiones/" + element.id_camion + "/";
					option.text = element.camiones;
					console.log(option);
					this.options.push(option);
				});
			},

			async putCamion() {
				let respuesta = "vacio";
				await axios
					.put(
						"https://arandadistribucion.pythonanywhere.com/camiones/" +
							this.gastoSaliente.id_gasto +
							"/",
						this.camion
					)
					.then(function (data) {
						swal("Operación Exitosa", " ", "success");
					})
					.catch(function (error) {
						const mje =
							error.response.status < 500
								? "Los datos no son válidos"
								: "Se ha detectado un problema ";
						swal("¡ERROR!", mje, "error");
						respuesta = error.response.data;
					});
				this.cargarFeedback(respuesta);
				this.updateTable();
				// console.log("respuesta:");
				// console.log(respuesta);
			},

			cargarFeedback(respuestaAPI) {
				console.log("respuestaAPI");
				console.log(respuestaAPI);
				let valido;
				for (let key in this.validacion) {
					valido = !respuestaAPI.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					console.log(key);

					if (!valido) this.validacion[key].mensaje = respuestaAPI[key][0];
				}
			},
		},
	};
</script>

<style></style>
